export const fizzBuzz = (limit) => {
  const fizzBuzzedNumbers = [];

  for (let i = 0; i < limit; i++) {
    if ((i % 3 === 0) && (i % 5 === 0)) {
      fizzBuzzedNumbers.push("FB");
    } else if (i % 3 === 0) {
      fizzBuzzedNumbers.push("F");
    } else if (i % 5 === 0) {
      fizzBuzzedNumbers.push("B");
    } else {
      fizzBuzzedNumbers.push(i);
    }

  }
  return fizzBuzzedNumbers;
};



export const fizzleBuzz = (limit) => {
  const fizzBuzzedNumbers = [];
  let output = '';
  function fizzBuzz() {
    var output;
    for (var i = 1; i <= 100; i++) {
      output = '';
      if (i % 3 === 0) {
        output += 'Fizz';
      }
      if (i % 5 === 0) {
        output += 'Buzz';
      }
      if (output === '') {
        output += i;
      }
      print(output);
    }
  }
  return fizzBuzzedNumbers;
};






export default { fizzleBuzz};
